.sample__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.sample__container iframe{
    width: 62rem;
    height: 39rem;
    border-style: none;
}
.task1__description{
    margin-top: 10rem;
    margin-left: 12rem;
    margin-right: 12rem;
}
.sample1, .sample3{
    margin-right: 12rem;
    margin-top: 0;
}
.sample2{
    margin-right: 41rem;
    margin-top: 0;
}
.task1__button{

    display: flex;
    flex-direction: row;
    text-decoration: none;
    color: white;
    border: #276061 solid 3px ;
    background-color: #276061;
    width: 8rem;
    border-radius: 1rem;
    margin: 1rem;
    padding: 0.5rem 0.8rem;
    justify-content: space-around;
    align-items: center;
  }
  .task1__button:hover{
    border-color: #c1142f;
    background-color: white;
    color: black;
  }