.task3__container{
    display: flex;
    flex-direction: row-reverse;
    margin-top: 2rem;
    align-items: center;
    margin-bottom: 0.5rem;
    justify-content: center;
}
.task3{
    margin-top: 6rem;
    width: 20rem;
    height: 39rem;
    border-style: none;
}
.task3__description{
    margin-top: 10rem;
    margin-left: 3.5rem;
    margin-bottom: 9rem;
    width: 20%;
}
.task3__button{

    display: flex;
    flex-direction: row;
    text-decoration: none;
    color: white;
    border: #276061 solid 3px ;
    background-color: #276061;
    width: 8rem;
    border-radius: 1rem;
    margin: 1.5rem 1rem;
    padding: 0.5rem 0.8rem;
    justify-content: space-around;
    align-items: center;
  }
  .task3__button:hover{
    border-color: #c1142f;
    background-color: white;
    color: black;
  }
  .buttons__container{
    display: flex;
    flex-direction: column;
  }
  .buttons__container a {
    cursor: pointer;
    margin: 0.5rem;
  }