@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue:ital,wght@0,200;0,300;0,700;1,500;1,700&display=swap');
body{
  /* font-family: 'Epilogue', sans-serif;
   */
   font-family: 'Roboto', sans-serif;
}
.navbar{
  background-color: rgb(238, 236, 236);
  border-bottom: #276061 solid 5px;
  position: fixed;
  width: 100%;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.navButtons__container{
  font-family: 'Roboto' ;
  display: flex;
  align-items: center;
  margin-right: 5rem;
  font-weight: 500;
  font-size: 1.1rem;

}
.navButtons__container a {
  margin-left: 1rem;
  border-right: #c1142f solid 3px;
  padding-right: 1rem;
  
}
.navButtons__container a:hover {
  color: #c1142f;
}
.navabar__logo{
  width: 16rem;
  margin: 1rem 7rem;
}
.navbar__button{
  text-decoration: none;
  color: inherit;
}
/* body{
  background-color: white;
  font-family: 'Roboto', sans-serif;
  color: #3a3a3c;
} */
.introduction__container{
  display: flex;
  justify-content: center;
}
.introduction{
  display: flex;
  flex-direction: column;
  width: 70%;
  align-items: center;
  margin-top: 5rem;
  margin-bottom: 15rem;
}

.introduction__article{
  margin-top: 5rem;
  margin-bottom: 2.7rem;
}
.introduction__article h1{
  font-size: 2rem;
}
.introduction__article p{
  font-size: 1.2rem;
}


.sidebar{
  display: flex;
  flex-direction: column;
  padding: 0.8rem;
  border-right: #c1142f solid 4px;
  /* background-color: rgb(183, 183, 183); */
  width: 2rem;
  position: fixed;
  top: 25rem;
}
.sidebar__icon{
  width: 1.9rem;
  cursor: pointer;
}
.sidebar__icon:hover{
  color: #c1142f;
}
.sidebar__linkedin{
  border-radius: 0.58rem;
}

.projects__button{
  font-family: 'Roboto' ;
  display: flex;
  flex-direction: row;
  text-decoration: none;
  color: white;
  border: #276061 solid 3px ;
  background-color: #276061;
  width: 8rem;
  border-radius: 1rem;
  margin: 1rem;
  padding: 0.5rem 0.8rem;
  justify-content: space-around;
  align-items: center;
}
.projects__button:hover{
  border-color: #c1142f;
  background-color: white;
  color: black;
}
.projects__button img{
  width: 1.5rem;
}
.buttons__container1{
  display: flex;
  flex-direction: row;
}

.footer{
  background-color: #276061;
  color: whitesmoke;
  margin: 0;
  height: 1rem;
  padding: 1rem;
  display: flex;
  align-items: center;
}
.footer p{
  font-size: 0.8rem;
  margin: 0;
}
