.navbar{
  background-color: rgb(238, 236, 236);
  border-bottom: #276061 solid 5px;
  position: fixed;
  width: 100%;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.navButtons__container{
  font-family: 'Roboto' ;
  display: flex;
  align-items: center;
  margin-right: 5rem;
  font-weight: 500;
  font-size: 1.1rem;

}
.navButtons__container a {
  margin-left: 1rem;
  border-right: #c1142f solid 3px;
  padding-right: 1rem;
  
}
.navButtons__container a:hover {
  color: #c1142f;
}
.navabar__logo{
  width: 16rem;
  margin: 1rem 7rem;
}
.navbar__button{
  text-decoration: none;
  color: inherit;
}
/* body{
  background-color: white;
