.introduction__container{
  display: flex;
  justify-content: center;
}
.introduction{
  display: flex;
  flex-direction: column;
  width: 70%;
  align-items: center;
  margin-top: 5rem;
  margin-bottom: 15rem;
}

.introduction__article{
  margin-top: 5rem;
  margin-bottom: 2.7rem;
}
.introduction__article h1{
  font-size: 2rem;
}
.introduction__article p{
  font-size: 1.2rem;
}


.sidebar{
  display: flex;
  flex-direction: column;
  padding: 0.8rem;
  border-right: #c1142f solid 4px;
  /* background-color: rgb(183, 183, 183); */
  width: 2rem;
  position: fixed;
  top: 25rem;
}
.sidebar__icon{
  width: 1.9rem;
  cursor: pointer;
}
.sidebar__icon:hover{
  color: #c1142f;
}
.sidebar__linkedin{
  border-radius: 0.58rem;
}

.projects__button{
  font-family: 'Roboto' ;
  display: flex;
  flex-direction: row;
  text-decoration: none;
  color: white;
  border: #276061 solid 3px ;
  background-color: #276061;
  width: 8rem;
  border-radius: 1rem;
  margin: 1rem;
  padding: 0.5rem 0.8rem;
  justify-content: space-around;
  align-items: center;
}
.projects__button:hover{
  border-color: #c1142f;
  background-color: white;
  color: black;
}
.projects__button img{
  width: 1.5rem;
}
.buttons__container1{
  display: flex;
  flex-direction: row;
}

.footer{
  background-color: #276061;
  color: whitesmoke;
  margin: 0;
  height: 1rem;
  padding: 1rem;
  display: flex;
  align-items: center;
}
.footer p{
  font-size: 0.8rem;
  margin: 0;
}

.sample__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.sample__container iframe{
    width: 62rem;
    height: 39rem;
    border-style: none;
}
.task1__description{
    margin-top: 10rem;
    margin-left: 12rem;
    margin-right: 12rem;
}
