}
.task3__button{

    display: flex;
    flex-direction: row;
    text-decoration: none;
    color: white;
    border: #276061 solid 3px ;
    background-color: #276061;
    width: 8rem;
    border-radius: 1rem;
    margin: 1.5rem 1rem;
    padding: 0.5rem 0.8rem;
