.task2__container{
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
    align-items: center;
    margin-bottom: 6.7rem;
}
.task2{
    width: 63rem;
    height: 19rem;
    border-style: none;
}
.task2__description{
    margin-top: 10rem;
    margin-left: 12rem;
}
.task2__button{

    display: flex;
    flex-direction: row;
    text-decoration: none;
    color: white;
    border: #276061 solid 3px ;
    background-color: #276061;
    width: 8rem;
    border-radius: 1rem;
    margin: 1.5rem 1rem;
    padding: 0.5rem 0.8rem;
    justify-content: space-around;
    align-items: center;
  }
  .task2__button:hover{
    border-color: #c1142f;
    background-color: white;
    color: black;
  }